@import '../../styles/variables.scss';
.aside{
    height: 100%;
    width: 250px;
    padding-right: 5px;
    padding-left: 15px;
    padding-top: 30px;
    border-right: 1px solid rgba(178, 183, 255, 0.425);
}
.list{
    display: flex;
    flex-direction: column;
    gap: 10px;
}
.item{
    align-items: center;
    display: flex;
    gap: 5px;
}
.img{
    min-width: 30px;
    min-height: 30px;
    width: 30px;
    height: 30px;
    fill: $sidebar-img;
}
.text{
    font-size: 17px;
    color: $sidebar-text;
    transition: .2s;
    border-bottom: 1px solid transparent;
    cursor: pointer;
    &:hover{
        color: $sidebar-text-hover;
        text-decoration-line:underline;
    }
}