.main{
    padding: 15px 20px 20px 20px;
    width: 100%;
    height: 100%;
}
.container{
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
}
.item{
    padding-top: 20px;
    width: 200px;
    align-items: center;
    height: 300px;
    border-radius: 15px;
}
.img{
    width: 100%;
    height: 200px;
    object-fit: contain;
}
.heart{
    height: 30px;
    width: 30px;
}
.text{
    margin-top: 10px;
}
.name{
    display: block;
    height: 32px;
    margin-bottom: 8px;
    overflow: hidden;
    font-size: 17px;
    line-height: 16px;
    color: #221f1f;
}
.price{
    font-size: 22px;

}
.actions{
    margin-top: 10px;
    display: flex;
    justify-content: space-between;
    padding-right: 20px;
}
.btn{
    font-size: 16px;
    color: #fff;
    font-weight: 600;
    padding: 8px 35px;
    border: none;
    border-radius: 10px;
    background-color: rgb(39, 41, 189);
    transition: .2s;
    &:hover{
        background-color: rgb(19, 23, 214) ;
    }
    &:active{
        border: 1px solid rgb(0, 4, 255);
    }
}
